<script>
  import $ from 'jquery';
  import 'slick-carousel';
  import FooterService from  "@/services/footer_add.js";
  import Takephoto from "@/services/take_photo.js";
  import MainService from "@/services/main_add.js";
  
  export default {
    name: 'main_page',    
    
    data() {
      return {
        selected:"consumer",
        posts:[],
        serverpath:"http://185.146.3.195:8081/assets/",
        footer_post:{data_invest:[],data_brand:[],data_production:[]},
        green_photo: "",
        main_photo: "",
        
                       
      }
    },
    watch: {
      selected:async function(){  
        //window.console.log("SKKS")
        this.footer_post = await FooterService.getPosts({name:this.selected});
      },    
    },
    methods: {
      
    },
    async beforeCreate(){
      try {
        this.main_photo = await Takephoto.getPosts("main_of_main");
        this.green_photo = await Takephoto.getPosts("main_page");                
        this.footer_post = await FooterService.getPosts({name:"consumer"});
        
        this.posts = await MainService.getPosts();
        
        //window.console.log(this.posts);     
      }
      catch(err) {
        window.console.log("Error");
      }
    },
    created:function () {    
      document.title = "Magic";      
                
    },
    async mounted() {
      
      $('.carousel').slick({
      slidesToShow: 1,
      mobileFirst: true,
      autoplay: true,
      arrows: false,  
      dots: true,
      adaptive: true
    });
    var scroll = function(){
      var btn = $('#buttonTop');

      $(window).scroll(function() {
        if ($(window).scrollTop() > 300) {
          btn.addClass('show');
        } else {
          btn.removeClass('show');
        }
      });

      btn.on('click', function(e) {
        e.preventDefault();
        $('html, body').animate({scrollTop:0}, '300');
      });
    };
    scroll();
    },
}
</script>

