import axios from "axios";

const url = "http://185.146.3.195:8081/footer";

class FooterService {
  static update(data) {
    return axios.post(url, data);
  }
  static async getPosts(arg) {
    const res = await axios.get(url, {params:arg});
    window.console.log(res.data)
    return res.data;
  }
}
export default FooterService; 