<template>
  <div id = "consumer">
    <header class="main-header">
    <div class="container space-between"> 
      <div class="big-screen-flex">
        <router-link :to="{name:'main'}" class="logo-link">
          <img src="@/assets/content/logo.png" alt="" class="logo-img">
        </router-link>
        <div>
          <input id="menu-toggle" type="checkbox" class="hidden">
          <label for="menu-toggle" class="menu-toggle-btn"><i class="fas fa-bars bar"></i></label>
          <nav class="menu">
            <ul class="menu-list">
              <li class="menu-item"><router-link :to="{name:'consumer'}" class="menu-link">Потребительский брендинг</router-link></li>
              <li class="menu-item"><router-link :to="{name:'corporate'}" class="menu-link">Корпоративный брендинг</router-link></li>
              <!--<li class="menu-item"><a href="#" class="menu-link">Спортивный брендинг</a></li>-->
              <li class="menu-item"><router-link :to="{name:'production'}" class="menu-link">Продакшн</router-link></li>
            </ul>
            <ul class="menu-list">
              <li><router-link :to="{name:'clients'}" class="menu-link">Наши клиенты</router-link></li>
              <li class="menu-item"><a href="#" class="menu-link">Наши контакты</a></li>
            </ul>
          </nav>
        </div>
      </div>
      <div class="language-parent">
        <a class="lang" href="#">ru</a>
        <a class="lang" href="#">en</a>
      </div>
    </div>
  </header>
  <div class="container">
    <div class="motto">
      <img class="motto-img" :src="serverpath + green_photo" alt="motto">
    </div>
    <div class="main-content">


      <div class="content-block" v-for="post in posts" :key="post._id">
        <router-link class="arrow-link" :to="{ path: 'consumer_product/' + post._id }">
        <img class="content-img" :src="serverpath + post.main_path" alt="nestle">
        <div class="img-hover scale">
          <h3 class="hover-header">{{post.text.split('//')[0]}}</h3>
          <p class="hover-under-title">{{post.text.split('//')[1]}}</p>
          <!--<router-link class="arrow-link" :to="{ path: 'consumer_product/' + post._id }"><i class="arrow fas fa-arrow-right"></i></router-link>-->
        </div>
        </router-link>
      </div>



    </div>
  </div>
  <div class="footer-bg">
    <footer class="footer container">
      <div class="footer-left-side">
        <div class="social-media">
          <a class="soc-link" href="#"><i class="fab fa-facebook-f footer-icon"></i></a>
          <a class="soc-link" href="#"><i class="fab fa-instagram footer-icon"></i></a>
          <a class="soc-link" href="#"><i class="fab fa-behance footer-icon"></i></a>
          <a class="soc-link" href="#"><i class="fab fa-pinterest-p footer-icon"></i></a>
          <a class="soc-link" href="#"><i class="fab fa-linkedin-in footer-icon"></i></a>
          <a class="soc-link" href="#"><i class="fab fa-google-plus-g footer-icon"></i></a>
        </div>
        <nav class="mini-menu-nav">
          <ul class="mini-menu-list">
            <li><router-link :to="{name:'consumer'}" class="footer-icons-link" href="">Потребительский брендинг</router-link></li>
            <li><router-link :to="{name:'corporate'}" class="footer-icons-link" href="">Корпоративный брендинг</router-link></li>
            <!--<li><a class="footer-icons-link" href="">Спортивный брендинг</a></li>-->
            <li><router-link :to="{name:'production'}" class="footer-icons-link">Продакшн</router-link></li>
            <li><router-link :to="{name:'clients'}" class="footer-icons-link" href="">Наши клиенты</router-link></li>
            <li><a class="footer-icons-link" href="">Наши контакты</a></li>
          </ul>
        </nav>
      </div>
      <div>
        <img src="@/assets/content/logo_second.png" alt="logo">
        <p>8 727 248 10 14 <br> info@likeagencu.kz</p>
        <p class="copyright"><i class="far fa-copyright"></i> 2011-2018 LIKE!</p>
      </div>
    </footer>
  </div>
  <i id="buttonTop" class="fas fa-chevron-circle-up"></i>
  
  </div>
</template>


<script>
  import $ from 'jquery';
  import ConsumerService from "@/services/cunsomer_add.js"; 
  import Takephoto from "@/services/take_photo.js";

  export default {
    name: 'consumer',
    /*components: {
      ConsumerProduct
    },*/
    data() {
      return {
        posts:[],
        serverpath:"http://185.146.3.195:8081/assets/",
        green_photo:"",
      }
    },
    methods: {
     
    },
    async beforeCreate(){
      try {
        this.posts = await ConsumerService.getPosts(); 
        this.green_photo = await Takephoto.getPosts("consumer"); 
      }
      catch(err) {
        window.console.log("Error");
      }
    },
    async mounted() {
      var scroll = function(){
        var btn = $('#buttonTop');

        $(window).scroll(function() {
          if ($(window).scrollTop() > 300) {
            btn.addClass('show');
          } else {
            btn.removeClass('show');
          }
        });

        btn.on('click', function(e) {
          e.preventDefault();
          $('html, body').animate({scrollTop:0}, '300');
        });
      };
      scroll();
    }
  }
</script>

<style scoped></style>