import axios from "axios";

const url = "http://185.146.3.195:8081/take_photo/";

class Service{
  static async getPosts(page_type){
    const res =await axios.get(url, {params: page_type});
    window.console.log(res);
    window.console.log("magic");
    return res.data;
  }
  static insertPost(data) {
    return axios.post(url, data, {headers:{ "Content-Type":"multipart/form-data"}});
  }
} 
export default Service; 