<template>
  <div id = "inputter_footer"> 
    <h4>Изменить footer</h4>
      <h5>исследование
        <input type="text" v-model="investigation">
      </h5>
      
      <h5>Брэенд
        <input type="text" v-model="brand">
      </h5>
      
      <h5>Продакшн
        <input type="text" v-model="production">
      </h5>
      <select v-model="selected_delete"> 
        <option v-for="(option, index) in options" :key="index" :value="option.value">
          {{option.text}} 
        </option>
      </select>
      <br/>
      <br/>
      <button @click="updatefooters">update footer</button>
  </div>
</template>

<script>
import FooterService from "@/services/footer_add.js";
export default {
  name:"inputter_footer",
  data(){
    return {
      options:[
        {text:"потербительский", value: 1},
        {text:"Корпоротивный", value: 2},
        {text:"Продакшн", value: 3},
      ],
      selected_delete:1,
      brand:"",
      investigation:"",
      production:""
    };
  },
  methods: {
    async updatefooters(){        
      let data_invest = this.investigation.split('//');
      let data_brand = this.brand.split('//');
      let data_production = this.production.split('//');

      //await FooterService.update({data_invest, data_brand, data_production});
      if (this.selected_delete == 1) {
        await FooterService.update({data_invest, data_brand, data_production, name:"consumer"});
      }
      else if((this.selected_delete == 2)){
        await FooterService.update({data_invest, data_brand, data_production, name:"corporate"});
      }
      else {
        await FooterService.update({data_invest, data_brand, data_production, name:"production"});
      }
    }
  },
}
</script>
