import axios from "axios";

const url = "http://185.146.3.195:8081/consumer/add/";

class Service {
  static insertPost(data) {
    return axios.post(url,data ,{headers: {"Content-Type": "multipart/form-data"}});
  }
  static async getPosts() {
    const res = await axios.get(url);
    const data = res.data;
    return data;
  }
  static Deletepost(id_num) {
    window.console.log(`${url}${id_num}`);
    return axios.delete(`${url}${id_num}`);
    
  } 
}
export default Service; 