import axios from "axios";

const url = "http://185.146.3.195:8081/production/product/";

class Service {
  static async getWays(id_number) {
    const res = await axios.get(url);
    window.console.log(id_number);
    const data = res.data;
    return data;
  }
  static async getPosts(id_number) {
    const res = await axios.get(url + 'post');
    window.console.log(id_number);
    const data = res.data;
    return data;
  }
}
export default Service; 