<template>
  <div id = "clients">
    <h3>Clients</h3>
    <input type="file" accept="image/*" @change="when_select"/>
    <br>
    <br>
    <button @click="upload_client">Загрузить клиент </button>
    <br><br>
    
    <br><br>    
    <input type="text" v-model="delete_id">
    <br><br>
    <button @click="delete_client">Удалить клиента</button>
    
    <h4>{{status_send}}</h4>

  </div>
</template>

<script>
import Service from "@/services/clients/clients.js";

export default {
  name:"clients",
  data() {
    return {
      selectedFile:null,
      status_send: "",
      delete_id:"",
    };
  },
  methods: {
    when_select(event){
      window.console.log(event.target.files[0]);
      this.selectedFile = event.target.files[0];
    },
    async upload_client(){
      if (this.selectedFile.length != 0 && this.selectedFile != null){ 
        var data = new FormData();
        data.append('image', this.selectedFile);
        this.status_send = "WAIT BRO";
        await Service.insertPost(data);
        this.status_send = "It's Okey";
      }
    },
    async delete_client() {
      this.status_send = "WAIT BRO";
      await Service.Deletepost(this.delete_id);
      this.status_send = "It's Okey";
    }
  },
}
</script>