<template>
  <div id = "inputter"> 
    <div class ="adder">
      <h4>Добавить в главную</h4>
      <input type="text" v-model="message_main">
      <button @click="Upload_main">upload main</button> 
      <br/><br/>
      <select v-model="selected_main"> 
        <option v-for="(option, index) in options" :key="index" :value="option.value">
          {{option.text}} 
        </option>
      </select>
      <br/><br/>
      <input type="text" v-model="deleteid_main">
      <button @click="delete_main">delete_main</button> 

    </div>

    <div class="delete">
      <h4>Удалить</h4>
      <button @click="deleteUp">delete </button>
      <input type="text" v-model="deleteid">
      <br/><br/>
      <select v-model="selected_delete"> 
        <option v-for="(option, index) in options" :key="index" :value="option.value">
          {{option.text}} 
        </option>
      </select>
    </div>

    <div class="forfooter">
      <Inputterfooter/>
    </div>

    <div class ="adder">
      <h4>Добавить</h4>
      <h5>Дочерные файлы</h5>
      <input type="file" name="myImage" accept="image/*,video/*" @change="onFileSelected" required multiple/>
      <h5>Главный файл</h5>
      <input type="file" name="mainImage" accept="image/*" @change="onFileMainSelected"/> 
      <br/><br/>
      <input type="text" v-model="message">
      <button @click="Upload">upload </button>
      <br/><br/>
      <select v-model="selected"> 
        <option v-for="(option, index) in options" :key="index" :value="option.value">
          {{option.text}} 
        </option>
      </select>
      
      <h5 v-for ="(poster, index) in selectedFile" :key="index">{{poster.name}}</h5>
      <h4>{{status_send}}</h4>
    </div>
    <div class="page_photo">
      <PagePhoto />
    </div>
    <div class = "adder">
      <Clients/>
    </div>

  </div>
</template>



<script>
import ConsumerService from "@/services/cunsomer_add.js"; 
import CorporateService from "@/services/corporate_add.js";
import ProductionService from "@/services/production_add.js";

import MainService from "@/services/main_add.js";

import Inputterfooter from "@/components/input/inputter_footer.vue";
import PagePhoto from "@/components/input/page_photo.vue";

import Clients from "@/components/input/inputter_clients.vue";


  export default {
    name:"inputter",
    components: {
      Inputterfooter,
      PagePhoto,
      Clients,
    },
    data() {
      return {
        selectedFile:[],
        selectedMainFile:null,
        message:null,
        message_main:null,
        deleteid:null,
        status_send:"",
        investigation:null,
        brand:null,
        production:null,
        selected:1,
        options:[
          {text:"потербительский", value: 1},
          {text:"Корпоротивный", value: 2},
          {text:"Продакшн", value: 3},
        ],
        selected_delete:1,
        selected_main:1,
        deleteid_main:null,
      }
    },
    methods: {
      onFileSelected(event) {
        
        /*if (this.selectedFile.length == 0){
          this.selectedFile = event.target.files;
          window.console.log(this.selectedFile); 
        }
          else {
           window.console.log(event.target.files);
           for (var i = 0; i < event.target.files.length; ++i) {
              this.selectedFile.push(event.target.files[i]);
            }               
            window.console.log(this.selectedFile);  
          } */
        this.selectedFile = [...this.selectedFile,...event.target.files];       
        //window.console.log(this.selectedFile);  
        
        window.console.log(this.selectedFile);
      },
      onFileMainSelected(event) {
        window.console.log(event.target.files[0]);
        this.selectedMainFile = event.target.files[0];
      },
      async deleteUp() {
        if (this.selected_delete == 1) {
          ConsumerService.Deletepost(this.deleteid);
        }
        else if (this.selected_delete == 2){
          CorporateService.Deletepost(this.deleteid);
        }
        else {
          ProductionService.Deletepost(this.deleteid)
        }
      },
      async delete_main(){
        MainService.Deletepost(this.deleteid_main);
      },
      async Upload(){
        if (this.selectedFile.length != 0 && this.selectedMainFile != null){ 
          var data = new FormData();
          data.append('files',this.selectedMainFile);
          for (let i = 0; i < this.selectedFile.length; ++i) {
            data.append('files', this.selectedFile[i]);
          }
          
          data.append('text', this.message);
          this.status_send = "wait";
          window.console.log(this.selected)
          if (this.selected == 1) {
            await ConsumerService.insertPost(data);
          }
          else if (this.selected == 2){
            await CorporateService.insertPost(data);
          }
          else {
            await ProductionService.insertPost(data);
          }
          this.selectedFile = null;
          this.status_send = "It is OKey";
        }
          
          
      },
      async Upload_main(){
        if (this.selected_main == 1) {
          MainService.insertPost({id_number: this.message_main, type:1});
        }
        else if(this.selected_main == 2){
          MainService.insertPost({id_number: this.message_main, type:2});
        }
        else {
          MainService.insertPost({id_number: this.message_main, type:3});
        }
      },
      async getter() {
        var t = await ConsumerService.getPosts();
        window.console.log(t);
      },
      
    },
  }
</script>


<style>
  #inputter {display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-bottom: 6px}
  .page_photo{display: block;margin:3%;}
  .adder{margin:5%; display: block;}
  .delete{margin:5%;display: block;}
  .forgooter{margin:7%;display: block;}
</style>

