<template>
  <div id ="take_photo">
    <h3>Главные фотки на каждом</h3>
    <h4>
      Главная фотка в главной странице
      <input type="file" name="mainImage" accept="image/*" @change="addPhoto_main"/>
      <button @click="Main_image">загрузка фото</button>
    </h4>

    <h4>
      Побочные фотки на разных страницах
      <input type="file" name="mainImage" accept="image/*" @change="addPhoto"/>
      <button @click="little_image">загрузка фото</button>
    </h4>

    <select v-model="selected">
      <option v-for="(option, index) in options" :key="index" :value="option.value">
        {{option.text}} 
      </option>
    </select>
    <h3>{{Status}}</h3>
  </div>  
</template>

<script>
import Service from "@/services/take_photo.js"; 
export default {
  name:"take_photo",
  data() {
    return {
      selected:"main_page",
      options:[
          {text: "Главная страница", value:"main_page"},
          {text:"потербительский", value: "consumer"},
          {text:"Корпоротивный", value: "corporate"},
          {text:"Продакшн", value: "production"},
        ],
        Status:"",
      ToADD:null,
      ToADD_main:null,  
    }
  },
  methods: {
    addPhoto_main(event){
      //window.console.log(event.target)
      window.console.log(event.target.files[0])
      this.ToADD_main = event.target.files[0];
    },
    addPhoto(event){
      //window.console.log(event.target)
      window.console.log(event.target.files[0])    
      this.ToADD = event.target.files[0];
    },
    async Main_image() {
      var data = new FormData();
      data.append('files',this.ToADD_main);
      data.append('status',"main_of_main");

      this.Status = "wait"
      await Service.insertPost(data);
      this.Status = "It's okey";
    },
    async little_image(){
      var data = new FormData();
      data.append('files',this.ToADD);
      data.append('status',this.selected);
      
      this.Status = "wait"
      await Service.insertPost(data);
      this.Status = "It's okey";      
    },
  },
}
</script>



