import Vue from "vue";
import Router from "vue-router";
import main_page from "@/components/main_page.vue";

import inputter from "@/components/input/inputter.vue";

import consumer from "@/components/pages/consumer.vue";
import corporate from "@/components/pages/corporate.vue";
import production from "@/components/pages/production.vue";

import consumer_product from "@/components/each_page/consumer_product.vue";
import corporate_product from "@/components/each_page/corporate_product.vue";
import main_product from "@/components/main_product.vue";
import production_product from "@/components/each_page/production_product.vue";

import clients from "@/components/pages/clients.vue";


Vue.use(Router);

export default new Router({
  mode: "history",
  routes: [
    {
      path: "/main",
      name: "main",
      component: main_page,
      props: (route) => ({ title: route.params.username })
    },
    {
      path: "/ooeiif8844llll1000aaa5544588112Bahtik",
      component: inputter
    },
    {
      path: "/consumer",
      name: "consumer",
      component: consumer
    },
    {
      path: "/corporate",
      name: "corporate",
      component: corporate
    },
    {
      path: "/production",
      name: "production",
      component: production
    },
    {
      path: "/consumer_product/:id",
      name: "consumer_product",
      component: consumer_product,      
    },
    {
      path: "/corporate_product/:id",
      name: "corporate_product",
      component: corporate_product
    },
    {
      path: "/production_product/:id",
      name: "production_product",
      component: production_product
    },
    {
      path: "/main_product/:id",
      name: "main_product",
      component: main_product
    },
    {
      path: "/clients",
      name: "clients",
      component: clients
    },
    {
      path: "/*",
      component: main_page,

      beforeEnter: (to, from, next) => {
        return next("/main");
      }
    }
  ]
});
