<script>
import Service from "@/services/each_production.js";
import $ from 'jquery';
export default {
  name:"cunsomer_product",
  data() {
    return {
      ways:{left:-1, right:-1},
      posts:[],
      serverpath:"http://185.146.3.195:8081/assets/",
      mypath:"http://185.146.3.195:8080/production_product/",
    }
  },
  methods: {
    async updateData() {
      this.ways = await Service.getWays(this.$route.params.id);  
      this.posts = await Service.getPosts(this.$route.params.id);
      window.console.log("OK");
    }
  },
  
  async beforeCreate() {
    try {
      window.console.log(this.$route);
      window.console.log("stupid")
      this.ways = await Service.getWays(this.$route.params.id);  
      this.posts = await Service.getPosts(this.$route.params.id);

      window.console.log(this.ways);
      window.console.log(this.posts);
    }
    catch(err) {
      window.console.log("Error");
    }
  },
  async mounted() {
    var scroll = function(){
      var btn = $('#buttonTop');

      $(window).scroll(function() {
        if ($(window).scrollTop() > 300) {
          btn.addClass('show');
        } else {
          btn.removeClass('show');
        }
      });

      btn.on('click', function(e) {
        e.preventDefault();
        $('html, body').animate({scrollTop:0}, '300');
      });
    };
    scroll();
  },
  
}
</script>

